  background: rgba(0, 0, 0, 0.5);
}

.navbar_asos .toggle {
  width: 100%;
  height: 0;
  overflow: hidden;
  transition: 0.5s linear;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;
}

.navbar_asos .toggle ul {
  height: 100vh;
  background: linear-gradient(100.06deg, #09EBDF 0%, #366EAF 188.36%);
  width: 40%;
  padding-top: 100px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr;
      grid-template-columns: 1fr;
}

.navbar_asos .toggle li {
  list-style: none;
  position: relative;
  transition: 0.7s ease-in-out;
  display: flex;
  align-items: center;
  transition: 1s linear;
}

.navbar_asos .toggle li a {
  font-style: normal;
  font-weight: 500;
  font-size: 29px;
  line-height: 38px;
  letter-spacing: 0.236393px;
  color: #FFFFFF;
  height: 100%;
  padding: 0px 0px 0 50px;
  transition: 0.2s linear;
}

.navbar_asos .toggle li:hover a {
  margin-left: 10px;
}

.navbar_asos .toggle::-webkit-scrollbar {
